import { Component, h } from '@stencil/core';

/**
 * Help with the read more link
 */
@Component({
    tag: 'limel-example-read-more',
    shadow: true,
})
export class HelprExampleReadMore {
    public render() {
        const link = {
            href: 'https://www.wikipedia.org/',
            title: 'Read more in a new window',
            target: '_blank',
            text: 'Read more...',
        };

        return <limel-help value="You can get more" readMoreLink={link} />;
    }
}
